<template>
<section class="page-wrapper submit-restaurant">
  <h1 class="title">Cadastro de Estabelecimento</h1>
  <b-container>

<form-wizard @on-complete="onComplete" error-color="#a94442" title="Seja um parceiro Sacia Fome" subtitle="Nosso time entrará em contato o mais rápido possível" stepSize="lg" finishButtonText="ENVIAR" nextButtonText="PRÓXIMO" backButtonText="VOLTAR">
  <tab-content title="Informações do Proprietário" icon="ti-id-badge">

<b-form class="form-address">
            <h3 class="search-address-title"><strong>Sobre o dono do estabelecimento</strong></h3>
                    <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="E-mail" class="text-left">
                        <b-input type="email" size="lg" class="button-form-address" placeholder="exemplo@saciafome.com"></b-input>
                      </b-form-group>
                    </div>
                    </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Celular Pessoal" class="text-left">
                        <the-mask type="text" :mask="['(##) #####-####']" class="mb-1 form-control-lg form-control button-form-address" placeholder="(34) 9 9999-9999"/>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Nome" class="text-left">
                        <b-input type="text" size="lg" class="button-form-address" placeholder="Fulano"></b-input>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Sobrenome" class="text-left">
                        <b-input type="text" size="lg" class="button-form-address" placeholder="De Tal"></b-input>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-checkbox v-model="owner.whatsapp" value="accepted" unchecked-value="not_accepted">Permito ser contatado(a) via WhatsApp <i class="fa fa-whatsapp pl-1" style="color: #075E54; font-weight: bold;"></i></b-form-checkbox>
                </div>
                </div>

        </b-form>

  </tab-content>

  <tab-content title="Sobre o Estabelecimento" icon="ti-home">
      <b-form class="form-address">
            <h3 class="search-address-title"><strong>Dados do estabelecimento</strong></h3>
                    <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="UF" class="text-left">
                          <b-select v-model="restaurant.city.uf" :options="listStates" size="lg" class="button-form-address" ></b-select>
                      </b-form-group>
                    </div>
                    </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Cidade" class="text-left">
                        <b-select v-model="restaurant.city" :options="listCities(restaurant.city.uf)" size="lg" class="button-form-address"></b-select>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Nome" class="text-left">
                        <b-form-input type="text" placeholder="Restaurante Delícias Mineiras" size="lg" v-model="restaurant.name" class="button-form-address" ></b-form-input>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Endereço" class="text-left">
                        <b-form-input type="text" placeholder="Avenida Brasil" size="lg" v-model="restaurant.neighborhood" class="button-form-address" ></b-form-input>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Bairro" class="text-left">
                        <b-form-input type="text" placeholder="Santa Mônica" size="lg" v-model="restaurant.street" class="button-form-address" ></b-form-input>
                      </b-form-group>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                      <b-form-group label="Número" class="text-left">
                        <b-form-input type="text" placeholder="102" v-model="restaurant.number" size="lg" class="button-form-address"></b-form-input>
                      </b-form-group>
                </div>

                </div>

        </b-form>
   </tab-content>
   <tab-content title="Confirmação" icon="ti-check">
     Yuhuuu! This seems pretty damn simple
   </tab-content>
</form-wizard>
  </b-container>

  <app-how-get-apps/>
</section>
</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import { mapGetters } from 'vuex'
import { TheMask } from 'vue-the-mask'
export default {
  data () {
    return {
      owner: {
        email: '',
        phone: '',
        name: '',
        lastname: '',
        whatsapp: 'accepted'
      },
      restaurant: {
        name: '',
        city: {name: 'Uberlândia', uf: 'MG'},
        neighborhood: '',
        street: '',
        number: ''
      }
    }
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF'
    })
  },
  methods: {
    onComplete () {
      alert('Yay. Done!')
    },
    async submitRestaurant (restaurant) {
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer' + localStorage.getItem('token')
      let response = await _axios.post(
        '/lead', {
          name: restaurant.name,
          phone: restaurant.phone,
          street: restaurant.street,
          number: restaurant.number,
          neighborhood: restaurant.neighborhood,
          city: restaurant.city.name,
          state: restaurant.city.uf
        })
      console.log(response)
    }
  },
  components: {
    AppHowGetApps,
    TheMask
  }
}
</script>

<style scoped>
/* THEMIFY ICONS */
@import url(https://rawgit.com/lykmapipo/themify-icons/master/css/themify-icons.css);

.submit-restaurant {
  text-align: center;
}

.title {
  color: #4f4f4f;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-weight: 600;
  padding-top: 20px;
}

