  display: flex;
}
.wizard-tab-container {
    display: block;
    animation: fadeInRight 0.3s;
}
/* FORMS */

.form-address {
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px;
}

.form-address-cep {
  width: 100%;
  max-width: 370px;
  margin: 30px auto 60px;
}

.search-address .disabled {
    background: #cecece;
    -webkit-appearance: none;
    opacity: 1;
}

.search-address-title {
  color: #4f4f4f;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-weight: 600;
  padding-top: 2%;
  padding-bottom: 4%;
}

.animation-enter-active {
  animation: bounce-out-right 0.5s reverse;
}
.animation-leave-active {
  animation: bounce-out-right 0.5s;
}
@keyframes bounce-out-right {
  20% {
    opacity: 1;
    transform: translate3d(-20px, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(600px, 0, 0);
  }
}

.button-search {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
  font-family: 'Raleway', sans-serif;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 92%;
