}

.button-search2 {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
  font-family: 'Raleway', sans-serif;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
	background-color: #ffb42d;
}

.button-back {
  border: 0 !important;
	color: white;
	height: 50px;
