<template>
<div class="address-page wrapper mt-5 pt-4">
  <app-top-links/>
  <app-inner-page-address/>
<b-container class="mt-4 pb-5">
  <span class="my-address"><i class="fa fa-map-marker mr-2"> Novo Endereço</i></span>

    <b-button to="/enderecos" variant="success" class="mb-2 button-address">
      <i class="fa fa-undo mr-2"></i> Meus Endereços
    </b-button>

     <transition name="slide-fade">
        <b-form v-if="showCEP" class="form-address-cep">
            <div class="col-sm-12">
                <the-mask mask="#####-###" class="mb-1 form-control-lg form-control" type="text" placeholder="Digite seu CEP" v-model="address.cep"></the-mask>
            </div>
            <b-button variant="theme" size="lg" class="button-search mt-1 mb-1" @click="searchAddress()">Buscar</b-button>
            <b-form-text class="mt-2">
                <b-link @click="invertSearch()">
                    <span><center>Não sabe o CEP? Digite seu endereço</center></span>
                </b-link>
            </b-form-text>
        </b-form>
        </transition>

<transition name="animation">
        <b-form v-if="showAddress" class="form-address">
            <h3 class="search-address-title"><strong>Informe o endereço de entrega!</strong></h3>
                    <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-select v-model="address.uf" :options="listStates" size="lg" class="button-form-address" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </div>
                    </div>
                
                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-select v-model="address.city" :options="listCities(address.uf)" size="lg" class="button-form-address" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
                    </div>
                </div>

                <div class="form-group row" v-if="address.neighborhood">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Digite seu endereço" size="lg" v-model="address.neighborhood" class="button-form-address" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </div>
                </div>
                
                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Digite seu endereço" size="lg" v-model="address.street" class="button-form-address" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-form-input>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Nº" v-model="address.number" size="lg" class="button-form-address"></b-form-input>
                    </div>
                </div>

                <div class="form-group row">
                    <div class="col-xl-12 col-lg-12 col-md-12 col-sm-12 col-xs-12">
                        <b-form-input type="text" placeholder="Complemento" v-model="address.complement" size="lg" class="button-form-address"></b-form-input>
                    </div>
                </div>

                <b-btn class="button-search2 mb-2" variant="theme" size="lg" @click="saveAddress(address)">Cadastrar</b-btn>
                <b-btn class="button-back" variant="dark" size="lg" @click="invertAddress()">Voltar</b-btn>
        </b-form>
        </transition>

<b-modal id="modalAdd"
                ref="modalAdd"
                size="lg"
                title="Adicionar Endereço"
                title-tag="h4"
                ok-title="Registrar"
                @ok="saveAddress(address)"
                cancel-variant="danger"
                cancel-title="Cancelar">
        <b-form @submit.stop.prevent="handleAddModal">
   <div class="row">
      <div class="form-group col-lg-2">
        <the-mask mask="#####-###" v-model="address.cep" class="form-control" type="text" placeholder="Seu CEP"></the-mask>
      </div>
      <div class="form-group col-lg-8 pl-0">
        <input type="text" v-model="address.street" class="form-control"  placeholder="Nome da rua ou Avenida">
      </div>
      <div class="form-group col-lg-2 pl-0">
        <input type="number" v-model="address.number" class="form-control" placeholder="Nº">
      </div>
    </div>
    <div class="row">
      <div class="form-group col-lg-3">
          <input type="text" v-model="address.complement" class="form-control" placeholder="Casa/Apartamento">
      </div>
      <div class="form-group col-lg-6 pl-0">
        <input type="text" v-model="address.neighborhood" class="form-control" placeholder="Bairro">
      </div>
    </div>
    <div class="row">
      <div class="form-group col-lg-3">
        <label for="UF">Estado</label>
        <b-select v-model="address.city.uf" size="lg" :options="listStates" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
      </div>
      <div class="form-group col-lg-6 pl-0">
        <label for="Cidade">Cidade</label>
        <b-select v-model="address.city" size="lg" :options="listCities(address.city.uf)" :class="[{ disabled: disableFields }]" :disabled="disableFields"></b-select>
      </div>
    </div>
        </b-form>
    </b-modal>
  
  <b-modal ref="NewAddressSuccess" title="Cadastro de Endereço" ok-only>
    Endereço Cadastrado com Sucesso!
  </b-modal>


</b-container>

</div>

</template>

<script>
import { TheMask } from 'vue-the-mask'
import { mapGetters, mapActions } from 'vuex'
import GoogleMapsLoader from 'google-maps'
import AppTopLinks from '../TopLinks'
import AppInnerPageAddress from './InnerPageAddress'
export default {
  data () {
    return {
      disableFields: true,
      showCEP: true,
      showAddress: false,
      address: {
        cep: '',
        uf: 'MG',
        comeplement: '',
        city: {name: 'Uberlândia', uf: 'MG'},
        street: '',
        number: ''
      },
      auxAddress: {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        neighborhood: '',
        street: '',
        number: '',
        complement: ''
      }
    }
  },
  components: {
    TheMask,
    AppTopLinks,
    AppInnerPageAddress
  },
  computed: {
    ...mapGetters({
      listStates: 'allStates',
      listCities: 'allCitiesByUF',
      user: 'user'
    })
  },
  methods: {
    ...mapActions({
      allCities: 'setAllCities',
      allStates: 'setAllStates',
      currentPosition: 'setCurrentPosition'
    }),
    invertFields () {
      if (!this.address.cep) {
        this.disableFields = !this.disableFields
      } else {
        this.disableFields = true
      }
    },
    invertAddress () {
      this.address = {
        cep: '',
        uf: 'MG',
        city: {name: 'Uberlândia', uf: 'MG'},
        street: '',
        number: ''
      }
      this.showCEP = !this.showCEP
      this.showAddress = !this.showAddress
    },
    invertSearch () {
      this.invertFields()
      this.showCEP = !this.showCEP
      this.showAddress = !this.showAddress
    },
    async searchAddress () {
      if (!this.address.cep) {
        this.$swal({type: 'error', title: 'Oops...', text: 'Digite um CEP'})
      } else {
        let _axios = this.$http.create()
        let cep = this.address.cep
        let response = await _axios.get(
          'correios/cep/' + cep
        )
        if (response.data.status === true) {
          let _address = response.data.data
          this.address = {
            cep: _address.cep,
            uf: _address.uf,
            neighborhood: _address.bairro,
            city: { name: _address.localidade, uf: _address.uf },
            street: _address.logradouro,
            number: ''
          }
          this.invertSearch()
        } else {
          this.$swal({type: 'error', title: 'Oops...', text: 'Não achamos nenhum endereço com este CEP!'})
        }
      }
    },
    async saveAddress (address) {
      address = await this.populateCep(address)
      address.users_id = this.user.id
      // console.log(address)
      let response = []
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
      response = await _axios.post(
        'address', address)
      // console.log(response)
      if (response.status === 200) {
        // this.$refs.NewAddressSuccess.show()
        this.$swal({type: 'success', title: 'Novo endereço', text: 'Você cadastrou um endereço!'})
        this.$router.push('/enderecos')
      }
    },
    populate (address) {
      if (!this.address.cep && !this.address.number) {
        this.$swal({type: 'error', title: 'Oops...', text: 'CEP e número são obrigatórios!'})
      } else if (!this.address.number) {
        this.$swal({type: 'error', title: 'Oops...', text: 'Número é obrigatório!'})
      } else if (!this.address.cep) {
        this.$swal({type: 'error', title: 'Oops...', text: 'CEP é obrigatório!'})
      } else {
        this.address = JSON.parse(JSON.stringify(address))
        this.$refs.modalAdd.show()
      }
    },
    handleOkAddModal (address) {
      if (!this.address.cep && !this.address.street && !this.address.number && !this.address.neighborhood) {
        alert('Preencha os dados obrigatórios!')
      } else if (!this.address.cep) {
        alert('CEP é obrigatório!')
      } else if (!this.address.street) {
        alert('Nome da Rua ou avenida é obrigatório!')
      } else if (!this.address.number) {
        alert('Número é obrigatório!')
      } else if (!this.address.neighborhood) {
        alert('Bairro é obrigatório!')
      } else {
        this.saveAddress(address)
      }
    },
    getValueByKey (array, key) {
      for (var i = 0; i < array.length; i++) {
        for (var j = 0; j < array[i].types.length; j++) {
          if (array[i].types[j] === key) {
            return array[i].long_name.trim()
          }
        }
      }
      return null
    },
    async populateCep (auxAddress) {
      let strAddress = auxAddress.street + ' ' + auxAddress.number + ' ' + auxAddress.neighborhood + ' ' + auxAddress.city.name + ' ' + auxAddress.city.uf
      let vm = this
      return new Promise(function (resolve, reject) {
        GoogleMapsLoader.KEY = process.env.GOOGLE_MAPS_KEY
        GoogleMapsLoader.load(async function (google) {
          let geocoder = await new google.maps.Geocoder()
          geocoder.geocode({
            'address': strAddress
          }, function (results, status) {
            if (status === 'OK') {
              if (results[0]) {
                let array = results[0].address_components
                let address = {
                  street: vm.getValueByKey(array, 'route'),
                  number: auxAddress.number,
                  complement: auxAddress.complement,
                  neighborhood: vm.getValueByKey(array, 'sublocality'),
                  city: vm.getValueByKey(array, 'administrative_area_level_2'),
                  state: vm.getValueByKey(array, 'administrative_area_level_1'),
                  cep: vm.getValueByKey(array, 'postal_code'),
                  latitude: results[0].geometry.location.lat(),
                  longitude: results[0].geometry.location.lng()
                }
                address.street = address.street ? address.street : auxAddress.address
                address.neighborhood = address.neighborhood ? address.neighborhood : auxAddress.neighborhood

                resolve(address)
              }
            } else {
              reject({street: null})
            }
          })
        })
      })
    }
  },
  created () {
    this.allCities()
    this.allStates()
  }
}
</script>

<style scoped>
.form-address {
  width: 100%;
  max-width: 800px;
  margin: 0 auto 60px;
}

.form-address-cep {
  width: 100%;
  max-width: 370px;
  margin: 30px auto 60px;
  text-align: center;
}

.search-address .disabled {
    background: #cecece;
    -webkit-appearance: none;
    opacity: 1;
}

.search-address-title {
    font-family: "Give You Glory", cursive;
    color: #fff;
    padding-top: 4%;
}

.animation-enter-active {
  animation: bounce-out-right 0.5s reverse;
}
.animation-leave-active {
  animation: bounce-out-right 0.5s;
}
@keyframes bounce-out-right {
  20% {
    opacity: 1;
    transform: translate3d(-20px, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(600px, 0, 0);
  }
}

.slide-fade-enter-active {
  transition: all .4s ease;
}
.slide-fade-leave-active {
  transition: all .2s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active em versões anteriores a 2.1.8 */ {
  transform: translateX(10px);
  opacity: 0;
}

.button-search {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 92%;
	background-color: #ffb42d;
}

.button-search2 {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
	background-color: #ffb42d;
}

.button-back {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
  background-color: #343a40;
}

.button-form-address {
  /*border: 0 !important;*/
  border: 1.5px solid #eaebeb;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
	padding-bottom: 12px;
	padding-top: 12px;
	width: 100%;
}

.button-address {
  transition: .3s all ease;
  line-height: 39px;
  padding-left: 67px;
  padding-right: 67px;
  font-weight: 700;
  clear: both;
  margin-bottom: 15px;
  border: none;
  border-radius: 6px;
}

.my-address {
  font-size: 26px;
}
</style>



