<template>
    <section class="contact-page inner-page">
        <div class="container">
            <div class="row">
                <!-- REGISTER -->
                <div class="col-md-8 col-sm-8">
                    <div>
                        <div class="widget-body">
                            <div class="row">
                                <div class="col-sm-12 col-md-12">
                                    <h1 class="title">Cadastre-se</h1>
                                </div>
                            </div>
                            <b-row>
                                <div class="col-sm-12 col-md-12">
                            <div class="social-buttons m-t-10">
                                <fb-signin-button class="button-facebook"
                                :params="fbSignInParams"
                                @success="onSignInSuccess"
                                @error="onSignInError">
                                <i class="fa fa-facebook-square mr-1" aria-hidden="true"></i>
                                Continuar com Facebook
                                </fb-signin-button>
                            </div>
                                </div>
                                </b-row>
                            <hr class="hr-text" data-content="Ou use seu e-mail">
                            <form>
                                <div class="row">
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Nome</label>
                                        <input class="form-control input-form" type="text" id="example-text-input" v-model="user.first_name">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Sobrenome</label>
                                        <input class="form-control input-form" type="text" id="example-text-input-2" v-model="user.last_name">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">E-mail</label>
                                        <input type="email" class="form-control input-form" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="E-mail" v-model="user.email">
                                        <small id="emailHelp" class="form-text text-muted">Nós nunca compartilharemos seu e-mail com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputEmail1">Telefone</label>

                                        <the-mask class="form-control input-form" v-model="user.phone" :mask="['(##) ####-####', '(##) #####-####']" />
                                        <small class="form-text text-muted">Nós nunca compartilharemos seu telefone com ninguém.</small>
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword1">Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword1" v-model="user.password">
                                    </div>
                                    <div class="form-group col-sm-6">
                                        <label for="exampleInputPassword2">Confirmar Senha</label>
                                        <input type="password" class="form-control input-form" id="exampleInputPassword2" v-model="user.repeatPassword">
                                    </div>
                                </div>
                                <div class="row">
                                    <b-button class="button-register" @click="register()" >Cadastrar</b-button>
                                </div>
                            </form>
                        </div>
                        <!-- end: Widget -->
                    </div>
                    <!-- /REGISTER -->
                </div>
                <!-- WHY? -->
                <div class="col-md-4 col-sm-4">
                    <h4>O registro é rápido, fácil e gratuito.</h4>
                    <hr>
                    <b-img src="../../../assets/img/home/registration.jpeg" alt="Depois de registrado" class="img-fluid"/>
                    <p class="card-text">
                        {{ answer }}
                    </p>
                    <!--<div role="tablist">
                        <b-card no-body class="mb-1">
                            <b-card-header header-tag="header" class="p-0" role="tab">
                                <b-btn block v-b-toggle.accordion1 variant="theme">
                                    Preciso ser registrado para olhar os cardápios?
                                </b-btn>
                            </b-card-header>
                            <b-collapse id="accordion1" accordion="my-accordion" role="tabpanel">
                                <b-card-body>
                                    <p class="card-text">
                                        {{ answer }}
                                    </p>
                                </b-card-body>
                            </b-collapse>
                        </b-card>
                    </div>-->
                    <!-- end:Panel -->
                    <!--<h4 class="m-t-20">Entre em contato com o suporte ao cliente.</h4>
                    <p> Se você está procurando por mais ajuda ou tenha uma pergunta a fazer, por favor </p>
                    <p>
                      <b-btn style="border-radius: 8px; font-weight: normal; background-color: #ffb42d; color: white;" block to="fale-conosco" class="mb-2">
                        <i class="fa fa-envelope mr-1"></i> Contate-nos</b-btn>
                    </p>-->
                </div>
                <!-- /WHY? -->
            </div>
        </div>
    </section>
</template>

<script>
export default {
  data () {
    return {
      answer: 'Não é necessário ter uma conta para dar uma olhada nos restaurantes e cardápios.',
      user: {},
      confirmPassword: '',
      fbSignInParams: window.fbSignInParams
    }
  },
  methods: {
    register () {
      this.$http.post('/users', this.user).then((response) => {
        response = response.data
        if (response.status) {
          this.$store.dispatch('login', this.user).then(() => {
            // console.log('Response')
            this.$router.push('/restaurantes')
          })
        } else {
          this.$toasted.error(response.message).goAway(3000)
        }
      })
    },
    socialLogin (payload) {
      this.$store.dispatch('socialLogin', payload).then((data) => {
        this.$router.push('/restaurantes')
      })
    },
    onSignInSuccess (response) {
      window.FB.api('/me?fields=name,email,gender', user => {
        user.picture = `https://graph.facebook.com/${response.authResponse.userID}/picture?type=large`
        let json = {
          img: user.picture,
          first_name: user.name,
          last_name: '',
          email: user.email,
          gender: user.gender,
          authenticate: 'facebook',
          token: response.authResponse.accessToken
        }
        this.socialLogin(json)
      })
    },
    onSignInError (error) {
      console.log('OH NOES', error)
    },
    onLogout () {
      this.isConnected = false
    },
    onLogin () {
      this.facebookLogin()
    }
  }
}
</script>

<style scoped>

.contact-page h4 {
    text-align: center;
}

.button-facebook {
    color: #fff;
    background-image: linear-gradient(#4C69BA, #3B55A0);
    border: 0 !important;
    color: white;
    line-height: 20px;
    border-radius: 8px;
    box-sizing: border-box;
    display: block;
    font-size: 1.6em;
    font-weight: normal;
    padding-bottom: 12px;
    padding-top: 12px;
    width: 100%;
    text-align: center;
    cursor: pointer;
}

.btn-fb:hover {
    color: #fff;    
    background-image: linear-gradient(#4C69BA, #3B55A0);
}

.social-buttons .button-facebook {
    width: 100%;
}

.hr-text {
    font-size: 1rem;
    color: #A9B1B7;
    background: #fff;
    line-height: 1em;
    position: relative;
    outline: 0;
    border: 0;
    text-align: center;
    height: 1.5em;
    opacity: 0.5;
}
.hr-text:before {
  content: "";
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0));
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 1px;
}
.hr-text:after {
  content: attr(data-content);
  position: relative;
  display: inline-block;
  color: black;
  padding: 0 0.5em;
  line-height: 1.5em;
  color: black;
  background-color: #fcfcfa;
}

.widget {
    border: 0px solid #eaebeb;
    background: white;
    border-radius: 3px;
    margin-bottom: 20px;
    position: relative;
}

.widget-body {
    padding: 20px;
}

.title {
    color: #4f4f4f;
    text-align: center;
    font-weight: 600;
    font-size: 25px;
}

.input-form {
    border-radius: 8px;
    border: 1px solid #c5c5c5;
    box-sizing: border-box;
    color: #333;
    display: block;
    font-size: 19px;
    font-weight: normal;
    margin-bottom: 16px;
    padding-bottom: 15px;
    padding-left: 21px;
    padding-top: 15px;
    width: 100%;
}

.button-register {
    border: 0 !important;
    color: white;
    height: 50px;
    line-height: 20px;
    border-radius: 8px;
    box-sizing: border-box;
    display: block;
    font-size: 1.6em;
    font-weight: normal;
    padding-bottom: 12px;
    padding-top: 12px;
    width: 100%;
    background-color: #ffb42d;
    margin-left: 12px;
    margin-right: 12px;
}

.card-text {
    margin-top: 0.8rem;
    font-size: 1.1em;
    font-weight: 100;
    text-align: center;
}
</style>



