<template>
    <section class="page-wrapper">
        <h1 class="title">Favoritos</h1>
        <b-container class="mt-2 pb-4">
          <div class="bg-gray restaurant-entry" v-for="(favorite) in favoritesRestaurants" :key="favorite.id">
          <b-badge variant="transparent" class="open-ribbon" ><span>ABERTO</span></b-badge>
          <b-badge variant="transparent" class="closed-ribbon" ><span>FECHADO</span></b-badge>
            <b-row>
                <div class="col-sm-12 col-md-12 col-lg-8 text-sm-left">
                    <div class="entry-logo">
                        img
                    </div>
                    <!-- end:Logo -->
                    <div class="entry-dscr">
                        <h5>
                            nome restaurant
                        </h5>
                        <span>Categorias</span>
                        <ul class="list-inline">
                            <li class="list-inline-item">
                                <i class="fa fa-motorcycle"></i><span class="pl-1">Tempo min · Tempo max</span>
                            </li>
                        </ul>
                    </div>
                    <!-- end:Entry description -->
                </div>
                <div class="col-sm-12 col-md-12 col-lg-4 text-xs-center">
                    <div class="right-content bg-white">
                        <div class="right-review">
                            <div class="rating-block">
                                <span >
                                    rating
                                </span>
                                <span >
                                    <p style="color: #ffb42d;"><strong>NOVO!</strong> <i class="fa fa-fire ml-1"></i></p>
                                </span>
                            </div>
                              
                        </div>
                    </div>
                    <!-- end:right info -->
                </div>
            </b-row>
            </div>
            <div v-if="(!favoritesRestaurants || !favoritesRestaurants.length)">
              <h3 style="text-align: center; padding-top: 20px;">Não possui estabelecimentos em sua lista de favoritos.</h3>
            </div>
        </b-container>
    <app-how-get-apps/>
    </section>
</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
      favoritesRestaurants: []
    }
  },
  computed: {
    ...mapGetters({
      user: 'user'
    })
  },
  components: {
    AppHowGetApps
  },
  created () {
    this.favoritesRestaurants = this.user.favorites_restaurants_ids
  }
}
</script>

<style scoped>
.title {
  color: #4f4f4f;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-weight: 600;
  padding-top: 20px;
}

/* RIBBON BADGES */

    /* OPEN RESTAURANT */

.open-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.open-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: #79A70A;
  background: linear-gradient(#62C95A 0%, #228B22 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -30px;
}
.open-ribbon span::before {
  position: absolute;
  left: 0px;
  top: 100%;
  z-index: -1;
  border-left: 3px solid #228B22;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}
.open-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #228B22;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #228B22;
}

    /* ./OPEN RESTAURANT */

    /* CLOSED RESTAURANT */

.closed-ribbon {
  position: absolute;
  z-index: 1;
  overflow: hidden;
  width: 100px;
  height: 100px;
}
.closed-ribbon span {
  font-size: 10px;
  font-weight: bold;
  color: #FFF;
  text-transform: uppercase;
  text-align: center;
  line-height: 20px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
  width: 100px;
  display: block;
  background: linear-gradient(#F70505 0%, #B22222 100%);
  box-shadow: 0 3px 10px -5px rgba(0, 0, 0, 1);
  position: relative;
  top: 11px;
  left: -31px;
}
.closed-ribbon span::before {
  content: "";
  position: absolute; left: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid #B22222;
  border-right: 3px solid transparent;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}
.closed-ribbon span::after {
  content: "";
  position: absolute; right: 0px; top: 100%;
  z-index: -1;
  border-left: 3px solid transparent;
  border-right: 3px solid #B22222;
  border-bottom: 3px solid transparent;
  border-top: 3px solid #B22222;
}

 /* ./RIBBON BADGES */

.restaurant-entry {
    border: 1px solid #eaebeb;
    border-radius: 2px;
    margin-bottom: 20px;
}

.restaurant-entry .entry-logo {
    float: left;
    width: 110px;
    height: auto;
    border: 1px solid #eaebeb;
    border-radius: 2px;
    margin-top: 15px;
    margin-left: 15px;
    overflow: hidden;
}

.restaurant-entry .entry-logo b-img {
    max-width: 100%;
    display: block;
}

.restaurant-entry .entry-dscr {
    padding-left: 145px;
    margin-top: 15px;
    text-align: left;
}

.restaurant-entry .entry-dscr span {
    font-family: 'Montserrat', sans-serif;
}

.restaurant-entry .entry-dscr h5 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
}

.restaurant-entry .entry-dscr h5 a {
    font-family: 'Oswald', sans-serif;
    font-weight: 500;
    color: #414551;
    /*font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;*/
}

.restaurant-entry .entry-dscr ul li {
    font-size: 12px;
    margin-top: 5px;
}

.restaurant-entry .right-review {
    padding: 15px 10px;
    text-align: center;
    border-left: 1px solid #eaebeb;
    min-height: 145px;
    background-color: #fafaf8;
}

.restaurant-entry .right-review p {
    color: #414551;
    font-weight: 500;
    margin: 0 0 10px;
}

.restaurant-entry .right-review .rating-block {
    color: #ffb42d;
    background: transparent;
    border: none;
    padding: 5px 15px 5px;
}

@media (min-width: 320px) and (max-width: 991px) {
    .restaurant-entry .entry-logo {
        float: none;
        text-align: center;
        margin: 10px auto;
    }
    .restaurant-entry .entry-dscr {
        padding: 0 15px;
        text-align: center;
    }
    .restaurant-entry .right-review {
        padding: 15px 10px;
        border-left: transparent;
        border-top: 1px solid #eaebeb;
    }
    .restaurant-entry .entry-dscr ul li {
        margin-bottom: 9px;
    }
}
</style>



