<template>
    <div class="home">
        <app-banner></app-banner>
        <hide-at breakpoint="mediumAndBelow"><app-location-match/></hide-at>
        <hide-at breakpoint="mediumAndBelow"><app-popular/></hide-at>
        <app-how-it-works/>
        <hide-at breakpoint="mediumAndBelow"><app-featured-restaurants/></hide-at>
        <app-how-get-apps/>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import AppBanner from './partials/home/Banner'
import AppFeaturedRestaurants from './partials/home/FeaturedRestaurants'
import AppHowItWorks from './partials/home/HowItWorks'
import AppLocationMatch from './partials/home/LocationMatch'
import AppPopular from './partials/home/Popular'
import AppHowGetApps from '@/components/root/HowGetApps'
import {showAt, hideAt} from 'vue-breakpoints'
export default {
  components: {
    AppBanner,
    AppFeaturedRestaurants,
    AppHowItWorks,
    AppLocationMatch,
    AppPopular,
    AppHowGetApps,
    hideAt,
    showAt
  },
  data () {
    return {}
  },
  computed: {
    ...mapGetters({
      isLoggedIn: 'isLoggedIn'
    })
  },
  created () {
    if (this.isLoggedIn) {
      this.$router.push({ name: 'restaurants' })
    }
  }
}
</script>

<style scoped>

</style>



