<template>
<section class="page-wrapper text-center">
    <h1 class="title">Como faço para pedir?</h1>
    <b-container class="mt-2 pb-4">
        <show-at breakpoint="mediumAndAbove"><b-img rounded alt="Como pedir Passo a Passo Imagem" class="img-fluid" src="../../assets/img/how-it-works/icone-passo-a-passo.png"/></show-at>
        <hide-at breakpoint="mediumAndAbove"><b-img rounded alt="Como pedir Passo 1" class="img-fluid mb-3 mt-2" src="../../assets/img/how-it-works/passo1.png"/></hide-at>
        <hide-at breakpoint="mediumAndAbove"><b-img rounded alt="Como pedir Passo 2" class="img-fluid mb-3 mt-1" src="../../assets/img/how-it-works/passo2.png"/></hide-at>
        <hide-at breakpoint="mediumAndAbove"><b-img rounded alt="Como pedir Passo 3" class="img-fluid mb-3 mt-1" src="../../assets/img/how-it-works/passo3.png"/></hide-at>
        <hide-at breakpoint="mediumAndAbove"><b-img rounded alt="Como pedir Passo 4" class="img-fluid mb-3 mt-1" src="../../assets/img/how-it-works/passo4.png"/></hide-at>
        
        <h4 class="subtitle">Escolha entre as mais diversas opções de estabelecimentos e deixe o resto por nossa conta. Coordenamos para que tudo seja entregue na sua casa, de maneira rápida e prática.</h4>
        <div class="instructions">
        <h6 class="passos">1. Informe seu CEP</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">Informe seu CEP (ou permita o navegador saber sua localização) para que possamos listar os estabelecimentos que entregam na região buscada.</span>
        <h6 class="passos">2. Escolha o Estabelecimento</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">Indeciso sobre o que pedir? Navegue pela lista de estabelecimentos e escolha uma opção dentro do seu tipo de comida preferido.</span>
        <h6 class="passos">3. Selecione os Itens</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">Navegue pelo cardápio e adicione os itens de sua preferência. Tem restrição a algum tipo de condimento? Basta utilizar o campo de observação para deixar claras suas solicitações. Terminou de escolher? Confira o pedido no carrinho. Estando tudo correto, clique em "Finalizar".</span>
        <h6 class="passos">4. Faça o Cadastro ou Login</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">É seu primeiro acesso ao site? Não tem problema. Faça o cadastro, pois nós precisamos de alguns detalhes para que o seu pedido seja entregue na sua porta. Caso seja cadastrado, faça o login e ganhe tempo na escolha.</span>
        <h6 class="passos">5. Escolha a Forma de Pagamento</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">Confira o seu pedido e selecione entre nossas formas de pagamento (dinheiro, cartões ou ainda <i>cashback</i> se disponível) e clique em "Finalizar Pedido".</span>
        <h6 class="passos">6. Acompanhe seu Pedido</h6>
        <span style="font-family: 'Raleway', sans-serif; font-weight: 800;">Faremos de tudo para seu pedido chegar a você. Você ainda pode acompanhar seu pedido pelo site (ou pelo aplicativo).</span>
        </div>
    </b-container>
    <app-how-get-apps/>
    </section>
</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import {showAt, hideAt} from 'vue-breakpoints'
export default {
  components: {
    AppHowGetApps,
    hideAt,
    showAt
  }
}
</script>


<style scoped>
.title {
  color: #4f4f4f;
  font-family: 'Montserrat', sans-serif;
  text-align: center;
  font-weight: 600;
  padding-top: 20px;
}

.passos {
  color: #ffb42d;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 15pt;
}

.instructions {
  text-align: left;
  margin-top: 15px;
}

.subtitle {
  color: #4f4f4f;
  font-family: 'Raleway', sans-serif;
  text-align: justify;
  font-weight: 400;
  padding-top: 15px;
  padding-bottom: 15px;
}
</style>



