<template>
  <div class="orders page-wrapper">
    <b-container class="mt-3 cart-addon">
    <div class="text-center mb-3">
      <h2 style="font-family: 'Montserrat', sans-serif;">{{order.orders.restaurant.name | Upper}}</h2>
      <h4 style="font-family: 'Montserrat', sans-serif; color: green;">{{ getOrderStatus( order.status ) }}</h4>
      <span style="font-family: 'Raleway', sans-serif;"><i class="fa fa-motorcycle pr-2"></i>Tempo de entrega: {{order.orders.restaurant.delivery_min}} • {{order.orders.restaurant.delivery_max}}</span>
    </div>
    <hr>
    <b-card class="text-center" title="DETALHES DO PEDIDO" style="font-family: 'Raleway', sans-serif;">
      <p>N° do Pedido: <strong style="color: #93c35d;">#{{order.id}}</strong></p>
      <p>Horário do Pedido: <strong>{{order.createdAt | formatDate}}</strong></p>
      <p>Pagamento: <strong>{{order.orders.payment.name}}</strong></p>
      <p v-if="order.orders.money">Troco: <strong><vue-numeric read-only currency="R$" separator="." :value="order.orders.money" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong></p>
      <p v-else><strong>Não é necessário troco!</strong></p>
    </b-card><hr>
    <b-card class="text-center" title="OBSERVAÇÃO" v-if="order.note">
      <span style="font-family: 'Raleway', sans-serif; font-weight: 700;">{{order.note}}</span>
    </b-card>
    <hr>
    <div class="widget clearfix">
      <div class="widget-heading">
        <h3 class="widget-title text-dark">
          <i class="fa fa-list pr-2"></i>PRODUTOS
        </h3>
      <div class="clearfix"></div>
      </div>
      <div class="widget-body">
      <div class="row">
        <div class="col-sm-6">
        <div class="food-item white" v-for="(item) in order.orders.items" :key="item.id">
          <b-row>
                  <div class="col-xs-12 col-sm-6 col-lg-6 mb-3">
                    <div class="item-img pull-left">
                      <a class="restaurant-logo pull-left" v-if="item.thumb">
                        <b-img :src="item.thumb" :width="75" :height="75" :alt="item.name"/>
                      </a>
                      <a class="restaurant-logo pull-left" v-else>
                        <b-img src="../../../assets/img/checkout/imagemindisponivel.png" :width="75" :height="75" :alt="item.name"/>
                      </a>
                    </div>
                      <div class="rest-descr">
                        <h4 style="font-family: 'Montserrat', sans-serif;">
                          <a>{{item.name}}</a>
                        </h4>
                        <h6 style="font-family: 'Raleway', sans-serif;">{{item.ingredients}}</h6>
                        <h6 style="font-family: 'Raleway', sans-serif;" v-if="item.quantity > 0">Quantidade: {{item.quantity}}</h6>
                        <h6 v-if="item.options.required.name">
                          <span style="font-size: 15px; font-weight: 700;">
                            <i class="fa fa-exclamation-circle pr-1"></i>{{item.options.required.name}} · R$ {{item.options.required.price}}
                          </span>
                          <hr>
                          <span style="font-size: 16px; font-weight: 300;" v-for="(j) in item.options.optional" :key="j.id">
                            <i class="fa fa-plus-circle pr-1"></i>{{j.name}} · R$ {{j.price}}
                          </span>                            
                        </h6>
                      </div>
                  </div>
                      <div class="col-xs-6 col-sm-6 col-lg-6" v-if="item.options.required.price">
                        <span class="price pull-right">R$ {{item | sumOptionals}}</span>
                      </div>
                      <div class="col-xs-6 col-sm-6 col-lg-6" v-else>
                        <span class="price pull-right">R$ {{item | sumOptionals}}</span>
                      </div>
                </b-row>
        </div>
        </div>
        <div class="col-sm-6">
          <!-- ENDEREÇO DE ENTREGA -->
            <div v-if="order.flag_delivery == 1">
              <div class="cart-totals-title pb-1">
                    <h4 style="font-family: 'Montserrat', sans-serif;"><i class="fa fa-map pr-2"></i>ENDEREÇO DE ENTREGA</h4>
              </div>
              <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" style="font-family: 'Raleway', sans-serif;">
                      {{order.orders.address.street}} • Nº {{order.orders.address.number}}<br/><div v-if="order.orders.address.complement">
                      Complemento: {{order.orders.address.complement}}<br/></div><div v-else-if="!order.orders.address.complement">
                      Complemento: -</div>
                      Bairro: {{order.orders.address.neighborhood}}<br/>
                      {{order.orders.address.city}} • {{order.orders.address.state}}<br/>
              </b-card>
            </div>
          <!-- ./ENDEREÇO DE ENTREGA-->
          <!-- ENDEREÇO PARA RETIRADA -->
                <div v-else>
                    <div class="cart-totals-title pb-2">
                    <h4 style="font-family: 'Montserrat', sans-serif;"><i class="fa fa-building pr-2"></i>ENDEREÇO PARA RETIRADA</h4>
                  </div>
                    <b-card header-tag="header" footer-tag="footer" bg-variant="light" class="mt-1 mb-3" style="font-family: 'Raleway', sans-serif;">
                      {{order.orders.restaurant.address.street}} • Nº {{order.orders.restaurant.address.number}}<br/><div v-if="order.orders.restaurant.address.complement">
                      Complemento: {{order.orders.restaurant.address.complement}}<br/></div>
                      Bairro: {{order.orders.restaurant.address.neighborhood}}<br/>
                      {{order.orders.restaurant.address.city}} • {{order.orders.restaurant.address.state}}<br/>
                    </b-card>
                  </div>
                <!-- ./ENDEREÇO PARA RETIRADA -->
         <div class="cart-totals margin-b-20">
                                <div class="cart-totals-title">
                                    <h4 style="font-family: 'Montserrat', sans-serif;"><i class="fa fa-calculator pr-2"></i>RESUMO DO PEDIDO</h4>
                                </div>
                                <div class="cart-totals-fields">
                                    <table class="table">
                                        <tbody>
                                            <tr v-if="order.frete && order.orders.address.street">
                                                <td>SUBTOTAL</td>
                                                <td style="color: green;" v-if="order.sub_total"><strong><vue-numeric read-only currency="R$" separator="." :value="order.sub_total" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong></td>
                                            </tr>
                                            <tr v-if="order.discount == true">
                                                <td>DESCONTO: <i>{{discount.name}}</i></td>
                                          <!-- criar função no cart para fazer o cálculo do desconto -->
                                                <td style="color: red;"><strong>{{order.percentage_discount}}</strong></td>
                                            </tr>
                                            <tr v-if="order.frete">
                                                <td>FRETE</td>
                                                <td style="color: green;"><strong><vue-numeric read-only currency="R$" separator="." :value="order.frete" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong></td>
                                            </tr>
                                            <tr v-if="order.flag_delivery == 1">
                                                <td class="text-color">
                                                    <strong>TOTAL</strong>
                                                </td>
                                                <td class="text-color">
                                                    <strong><vue-numeric read-only currency="R$" separator="." :value="order.total" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                                                </td>
                                            </tr>
                                            <tr v-else>
                                                <td class="text-color">
                                                    <strong>TOTAL</strong>
                                                </td>
                                                <td class="text-color">
                                                    <strong><vue-numeric read-only currency="R$" separator="." :value="order.sub_total" v-bind:precision="2" v-bind:max="10000"></vue-numeric></strong>
                                                </td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
        </div>
      </div>
    </div>
    
    </div>

    </b-container>
    <a @click="$router.go(-1)" class="float"><i class="fa fa-2x fa-undo my-float"></i></a>
    <div class="label-container">
      <i class="fa fa-caret-left label-arrow"></i>
      <div class="label-text">Voltar</div>
    </div>
    <app-how-get-apps/>
  </div>
</template>

<script>
import AppHowGetApps from '@/components/root/HowGetApps'
import moment from 'moment'
export default {
  props: ['id'],
  data () {
    return {
      order: {},
      statusName: ['', 'PENDENTE - AGUARDANDO CONFIRMAÇÃO',
        'EM PREPARO - JÁ FOI CONFIRMADO',
        'SAIU PARA ENTREGA',
        'CONCLUÍDO - PEDIDO ENTREGUE',
        'CANCELADO - PEDIDO CANCELADO',
        'PRONTO - CLIENTE VEM BUSCAR'
      ]
    }
  },
  components: {
    AppHowGetApps
  },
  methods: {
    async getOrderById () {
      let _axios = this.$http.create()
      _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
      let response = await _axios.get(
        'orders/' + this.id
      )
      return response
    },
    getOrderStatus (status) {
      return this.statusName[status]
    }
  },
  filters: {
    Upper (value) {
      return value.toUpperCase()
    },
    formatDate (value) {
      if (value) {
        return moment(String(value)).locale('pt-br').format('LLL')
      }
    },
    sumOptionals (item) {
      let total = 0
      if (item.options.required.price && !item.options.optional.length) {
        total += item.options.required.price * item.quantity
      } else if (item.options.required.price && item.options.optional.length) {
        let optionalsArrayPrice = item.options.optional.map(function (i) {
          return i.price
        })
        let optionalsPrice = optionalsArrayPrice.reduce(function (acc, val) { return acc + val }, 0)
        total += (item.options.required.price + optionalsPrice) * item.quantity
      } else {
        total += item.price * item.quantity
      }
      return total
    }
  },
  created () {
    /* getOrderById */
    this.getOrderById().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.order = response1
    })
    /* ./getOrderById */
  }
}
</script>

<style scoped>
.cart-addon .food-item {
    margin-bottom: 20px;
    border-bottom: 1px solid #ededed;
    padding: 2px 0 15px;
}

.cart-addon .food-item:last-of-type {
    border-bottom: none;
}

.cart-addon .food-item .item-img b-img {
    max-width: 70px;
}

.cart-addon .food-item .rest-descr {
    margin-left: 80px;
}

.cart-addon .food-item h6 span {
    font-size: 16px;
    color: #4d4f56;
    display: block;
    margin: 4px 0 0 0;
    line-height: 20px;
    font-family: 'Raleway', sans-serif;
}

.cart-addon .food-item .price {
    font-weight: 600;
    margin-top: 15px;
    display: block;
    color: #25282b;
    font-family: 'Raleway', sans-serif;
}

.cart-totals {
  font-family: 'Montserrat', sans-serif;
}

.widget {
    border: 1px solid #eaebeb;
    background: #fafaf8;
    border-radius: 3px;
    margin-bottom: 20px;
}

.widget-heading {
    padding: 15px;
    color: #fff;
    border-radius: 3px;
    border-bottom: 1px solid #eaebeb;
}

.widget-heading .widget-title {
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    float: left;
    margin-top: 0;
    margin-bottom: 0;
    width: 100%;
}

.widget-body {
    padding: 20px;
}

.custom-control {
    display: inline-block;
}

.btn-back {
  border: 0 !important;
	color: white;
	height: 50px;
	line-height: 20px;
	border-radius: 8px;
	box-sizing: border-box;
	font-size: 1.3em;
	font-weight: normal;
  font-family: 'Raleway', sans-serif;
	padding-bottom: 12px;
	padding-top: 12px;
}

/* FLOATING BACK BUTTON */

.label-container{
	position:fixed;
	bottom:48px;
	left:105px;
	display:table;
	visibility: hidden;
}

.label-text{
	color:#FFF;
	background:rgba(51,51,51,0.5);
	display:table-cell;
	vertical-align:middle;
	padding:10px;
	border-radius:3px;
}

.label-arrow{
	display:table-cell;
	vertical-align:middle;
	color:#333;
	opacity:0.5;
}

.float{
  z-index: 999;
	position:fixed;
	width:70px;
	height:70px;
	bottom:40px;
	left:20px;
	background-color:#f53d3d;
	color:#FFF;
	border-radius:50px;
  cursor: pointer;
	text-align:center;
	box-shadow: 2px 2px 3px #999;
}

.my-float{
	font-size: 24px;
  margin-top: 20px;
  color:#fff;
}

a.float + div.label-container {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s, opacity 0.5s ease;
}

a.float:hover + div.label-container{
  visibility: visible;
  opacity: 1;
}

/* ./FLOATING BACK BUTTON */
</style>



