<template>
    <div class="result-show">
        <b-container>
            <b-row>
                <div class="col-sm-12 mt-2">
                    <p><strong>{{total}}</strong> estabelecimentos</p>
                </div>
            </b-row>
        </b-container>
    </div>
</template>

<script>
export default {
  props: ['total']
}
</script>

<style scoped>
.result-show .dropdown button {
    color: #333366;
    font-size: 14px;
    border: 0;
}

.result-show div i {
    margin-left: 10px;
    font-size: 22px;
    vertical-align: middle;
    color: #000;
}

.result-show {
    padding: 7px 0 10px;
    border-bottom: 1px solid #eaebeb;
    color: #333366;
    margin-bottom: 20px;
}

.result-show p {
    padding: 0;
    margin: 5px 0;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}

.result-show p strong {
    color: #ffb42d;
}

@media (min-width: 320px) and (max-width: 991px) {
    .result-show p {
        text-align: center;
    }
}
</style>



