<template>
  <div class="profile page-wrapper">
    <app-top-links/>
    <app-inner-page-hero :restaurant="restaurant" :ratings="ratings"/>
    <b-card bg-variant="transparent" no-body>
      <b-tabs pills v-model="tabIndex">
        <b-tab title="Cardápio" :title-link-class="linkClass(0)" >
          <b-container fluid class="m-t-30">
            <b-row>
              <div class="col-xs-9 col-sm-5 col-md-5 col-lg-8" >
                <app-widget-list-items :cuisines="cuisines" :items="items"/>
                <!--<app-widget-popular-tags/>-->
              </div>

                <div class="col-xs-12 col-sm-5 col-md-5 col-lg-4">
                  <app-widget-cart :restaurant="restaurant" id="cart"/>
                </div>
              <!--<div class="col-xs-12 col-md-12 col-lg-3">
                <app-sidebar-choose-cusine :cuisines="cuisines"></app-sidebar-choose-cusine>
              </div>-->
            </b-row>
            <!-- end:row -->
          </b-container>
        </b-tab>
        <b-tab title="Horários" :title-link-class="linkClass(1)">
          <div id="table-container">
            <table id="business-hours">
              <tr>
                <td class="day">Segunda</td>
                <td class="time">{{restaurant.monday}}</td>
              </tr>
              <tr>
                <td class="day">Terça</td>
                <td class="time">{{restaurant.tuesday}}</td>
              </tr>
              <tr>
                <td class="day">Quarta</td>
                <td class="time">{{restaurant.wednesday}}</td>
              </tr>
              <tr>
                <td class="day">Quinta</td>
                <td class="time">{{restaurant.thursday}}</td>
              </tr>
              <tr>
                <td class="day">Sexta</td>
                <td class="time">{{restaurant.friday}}</td>
              </tr>
              <tr>
                <td class="day">Sábado</td>
                <td class="time">{{restaurant.saturday}}</td>
              </tr>
              <tr>
                <td class="day last">Domingo</td>
                <td class="time last">{{restaurant.sunday}}</td>
              </tr>
            </table>
          </div>
        </b-tab>
        <b-tab title="Avaliações" :title-link-class="linkClass(2)">
          <b-container>
            <b-row>
              <b-col sm="12" md="6" lg="3" class="mt-4 mb-4 ratings-wrap" v-for="(rate) in ratings" :key="rate.id">
                    <div class="profile-img">
                              <figure v-if="rate.user.img">
                                  <b-img rounded="circle" :src="rate.user.img" :width="100" :height="100"/>
                              </figure>
                              <figure v-if="!rate.user.img">
                                <b-img rounded="circle" src=".././assets/img/profile/user_male.png" :width="100" :height="100"/>
                              </figure>
                      </div>
                      <ul style="color: black;">
                        <li style="font-weight: 600; font-family: 'Raleway', sans-serif;">{{rate.user.first_name}} {{rate.user.last_name}}</li>
                        <li style="font-size: 12px; font-family: 'Montserrat', sans-serif;" class="pt-1 pb-1">{{rate.createdAt | formatDate}}</li>
                        <li style="font-family: 'Raleway', sans-serif;"><i class="fa fa-cutlery pr-1"></i>Comida: <i style="color: #ffd953;" class="fa fa-star"></i> {{rate.rate_food}}</li>
                        <li style="font-family: 'Raleway', sans-serif;"><i class="fa fa-thumbs-up pr-1"></i>Custo/Benefício: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate }}</li>
                        <li style="font-family: 'Raleway', sans-serif;"><i class="fa fa-cube"></i> Embalagem: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate_packing }}</li>
                        <li style="font-family: 'Raleway', sans-serif;"><i class="fa fa-clock-o pr-1"></i>Tempo de Entrega: <i style="color: #ffd953;" class="fa fa-star"></i> {{ rate.rate_delivery }}</li>
                        <li style="font-family: 'Raleway', sans-serif;"><i class="fa fa-line-chart pr-1"></i>Média: <i style="color: #ffd953;" class="fa fa-star"></i> {{getMedia(rate)}}</li>
                        <li v-if="rate.description" style="font-family: 'Raleway', sans-serif;"><i class="fa fa-comment pr-1"></i>Comentário: "<span style="font-size: 13px; font-weight: 600;">{{ rate.description }}"</span></li>
                      </ul>
              </b-col>
              <b-col md="9" class="mb-4" v-if="!ratings || !ratings.length">
                <h3 style="color: black;">Ainda não possuímos nenhuma avaliação. <i class="fa fa-frown-o pl-1"></i></h3>
              </b-col>
            </b-row>
          </b-container>
        </b-tab>
    <!--<app-breadcrumb></app-breadcrumb>-->

  </b-tabs>
  </b-card>
    <!-- end:Container -->
    <!--<app-modal-cart-addon :items="items"></app-modal-cart-addon>-->
  <show-at breakpoint="small"><a @click="showCart" class="float"><i class="fa fa-2x fa-shopping-cart my-float"></i></a></show-at>
  </div>
</template>

<script>
import {showAt} from 'vue-breakpoints'
import moment from 'moment'
import { mapGetters } from 'vuex'
import AppTopLinks from './partials/TopLinks'
import AppInnerPageHero from './partials/profile/InnerPageHero'
import AppBreadcrumb from './partials/profile/Breadcrumb'
import AppSidebarChooseCusine from './partials/profile/SidebarChooseCusine'
import AppWidgetPopularTags from './partials/WidgetPopularTags'
import AppWidgetListItems from './partials/profile/WidgetListItems'
import AppWidgetCart from './partials/profile/WidgetCart'
import AppModalCartAddon from './partials/profile/ModalCartAddon'

export default {
  props: ['url_name'],
  data () {
    return {
      restaurant: [],
      cuisines: [],
      items: [],
      ratings: [],
      tabIndex: 0
    }
  },
  components: {
    AppTopLinks,
    AppInnerPageHero,
    AppBreadcrumb,
    AppSidebarChooseCusine,
    AppWidgetPopularTags,
    AppWidgetListItems,
    AppWidgetCart,
    AppModalCartAddon,
    showAt
  },
  computed: {
    ...mapGetters({
      currentPosition: 'currentPosition',
      isLoggedIn: 'isLoggedIn',
      getCartItems: 'getCartItems',
      getRestaurant: 'getRestaurant'
    })
  },
  methods: {
    address () {
      if (!this.isLoggedIn) {
        this.$swal({position: 'center', type: 'error', title: 'Você não tem uma conta', text: 'Entre ou se cadastre agora', showConfirmButton: false, timer: 2500})
        this.$router.push('/entrar')
      } else {
        this.$router.push('/enderecos')
      }
    },
    showCart () {
      document.getElementById('cart').scrollIntoView()
    },
    getMedia (rate) {
      if (rate.rate_delivery) {
        let media = (rate.rate + rate.rate_food + rate.rate_delivery) / 3
        return media.toFixed(1).replace('.', ',')
      } else {
        let media = (rate.rate + rate.rate_food) / 2
        return media.toFixed(1).replace('.', ',')
      }
    },
    linkClass (idx) {
      if (this.tabIndex === idx) {
        return ['bg-warning', 'text-dark']
      } else {
        return ['bg-light', 'text-dark']
      }
    },
    async getRestaurantById () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          '/restaurant/n/' + this.url_name + '/latitude/' + this.currentPosition.lat + '/longitude/' + this.currentPosition.lng
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getCuisinesById () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'cuisines/cuisines/items/' + this.getRestaurant.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getItemsByRestaurantId () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'items/restaurant/' + this.getRestaurant.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async getRatings () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.get(
          'rating/restaurant/' + this.getRestaurant.id
        )
      } catch (e) {
        console.log(e)
      }
      return response
    },
    async calcFrete () {
      let response = []
      try {
        let _axios = this.$http.create()
        _axios.defaults.headers.common['Authorization'] = 'Bearer ' + localStorage.getItem('token')
        response = await _axios.post(
          'calcula-frete', {
            destinationsLatitude: this.currentPosition.lat,
            destinationsLongitude: this.currentPosition.lng,
            restaurants_id: this.getRestaurant.id
          })
      } catch (e) {
        console.log(e)
      }
      return response
    }
  },
  filters: {
    formatDate (value) {
      if (value) {
        return moment(String(value)).locale('pt-br').fromNow()
      }
    }
  },
  beforeRouteLeave (to, from, next) {
    if (this.getCartItems.length > 0 && to.fullPath === '/restaurantes') {
      this.$swal({title: 'Sair', text: 'Deseja realmente sair desta tela? Ao sair seu carrinho será esvaziado', type: 'warning', showCancelButton: true, confirmButtonColor: '#3085d6', cancelButtonColor: '#d33', confirmButtonText: 'Sim, sair', cancelButtonText: 'Cancelar'})
      .then((result) => {
        if (result.value) {
          this.$swal('Você saiu', 'Seu carrinho foi esvaziado!', 'success')
          this.$store.dispatch('emptyCart')
          next()
        }
      })
    } else {
      next()
    }
  },
  created () {
    /* getRestaurantById */
    this.getRestaurantById().then((response) => {
      return response.data
    }).then(response => {
      if (response && response.data) {
        return response.data
      }
      return []
    }).then((response1) => {
      this.restaurant = response1
      this.$store.commit('setRestaurant', response1)

      this.calcFrete().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          return response.data
        }
        return []
      }).then((response1) => {
        this.$store.commit('setFrete', response1)
      })
      /* ./calcFrete */
      /* getCuisinesById */
      this.getCuisinesById().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          return response.data
        }
        return []
      }).then((response1) => {
        this.cuisines = response1
        this.$store.commit('setCuisines', response1)
      })
      /* ./getCuisinesById */
      /* getItemsByRestaurantId */
      this.getItemsByRestaurantId().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          return response.data
        }
        return []
      }).then((response1) => {
        this.items = response1
        this.$store.commit('setItems', response1)
      })
      /* ./getItemsByRestaurantId */
      /* getRatings */
      this.getRatings().then((response) => {
        return response.data
      }).then(response => {
        if (response && response.data) {
          return response.data
        }
        return []
      }).then((response1) => {
        this.ratings = response1
      })
    })
  }
}
</script>

<style scoped>
.ratings-wrap {
  text-align: center;
}

div#table-container{
  padding: 8px;
  background-color: #ffb42d;
}

table#business-hours{
  width: 100%;
  background-color: #f2f3f3;
  color: black;
  border-collapse: collapse;
}

table#business-hours tr td.day{
  border-right: 1px solid #cc6633;
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 18px;
  border-bottom: 1px solid #cc6633;
  padding-left: 15px;
}

table#business-hours tr td.time{
  text-align: center;
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  font-size: 18px;
  border-left: 1px solid #cc6633;
  border-bottom: 1px solid #cc6633;
}

table#business-hours tr td.time.last{
  border: none;
  border-left: 1px solid #cc6633;
}

table#business-hours tr td.day.last{
  border: none;
  border-right: 1px solid #cc6633;
}

.profile {
    max-width: 1235px !important;
    margin: 0 auto !important;
}

/* FLOATING CART BUTTON */

.float {
  z-index: 999;
	position:fixed;
	width:70px;
	height:70px;
	bottom:40px;
	left:20px;
	background-color:#f53d3d;
	color:#fff;
	border-radius:50px;
	text-align:center;
	box-shadow: 2px 2px 3px #999;
}

.my-float {
  margin-top: 20px;
  color:#fff;
}

/* ./FLOATING CART BUTTON */

</style>



