import Vue from 'vue'
import Router from 'vue-router'
import store from '../store'
import Home from '@/components/Home'
import Restaurants from '@/components/Restaurants'
import Profile from '@/components/Profile'
import FoodResults from '@/components/FoodResults'
import Checkout from '@/components/Checkout'
import Registration from '@/components/Registration'
import ForgotPassword from '@/components/ForgotPassword'
import Login from '@/components/Login'
import MyAccount from '@/components/partials/user/MyAccount'
import MyOrders from '@/components/partials/user/MyOrders'
import Address from '@/components/partials/user/Address'
import OrderDetails from '@/components/partials/user/OrderDetails'
import Favorites from '@/components/partials/user/Favorites'
import NewAddress from '@/components/partials/user/NewAddress'
import NewRestaurant from '@/components/partials/submit-restaurant/NewRestaurant'
import ContactUs from '@/components/ContactUs'
import Terms from '@/components/root/Terms'
import AboutUs from '@/components/root/AboutUs'
import HowItWorks from '@/components/root/HowItWorks.vue'
import PrivacyPolicy from '@/components/root/PrivacyPolicy.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  scrollBehavior () {
    return { x: 0, y: 0 }
  },
  routes: [{
    path: '/',
    name: 'home',
    component: Home,
    beforeEnter: (to, from, next) => {
      if (store.getters.isLoggedIn) {
        next({name: 'restaurants'})
      } else if (store.getters.getCartLength > 0) {
        Vue.swal({title: 'Sair', text: 'Deseja realmente sair desta tela? Ao sair seu carrinho será esvaziado', type: 'warning', showCancelButton: true, confirmButtonColor: '#3085d6', cancelButtonColor: '#d33', confirmButtonText: 'Sim, sair', cancelButtonText: 'Cancelar'})
      .then((result) => {
        if (result.value) {
          Vue.swal('Você saiu', 'Seu carrinho foi esvaziado!', 'success')
          store.dispatch('emptyCart')
          next()
        }
      })
      } else {
        next()
      }
    }
  }, {
    path: '/restaurantes',
    name: 'restaurants',
    component: Restaurants,
    beforeEnter: (to, from, next) => {
      if (store.getters.getCartLength > 0) {
        window.history.back()
      } else {
        next()
      }
    }
  }, {
    path: '/food-results',
    name: 'foodResults',
    component: FoodResults
  }, {
    path: '/pagamento',
    name: 'checkout',
    component: Checkout,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else if (store.getters.getCartLength === 0) {
        next(false)
      } else {
        next()
      }
    }
  }, {
    path: '/cadastro',
    name: 'registration',
    component: Registration
  }, {
    path: '/entrar',
    name: 'enter',
    component: Login
  }, {
    path: '/esqueci-senha',
    name: 'forgot-password',
    component: ForgotPassword
  }, {
    path: '/fale-conosco',
    name: 'contact-us',
    component: ContactUs
  }, {
    path: '/minha-conta',
    name: 'myaccount',
    component: MyAccount,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/favoritos',
    name: 'favorites',
    component: Favorites,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/pedidos',
    name: 'myorders',
    component: MyOrders,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/enderecos',
    name: 'address',
    component: Address,
    beforeEnter: (to, from, next) => {
      // verifica se esta logado
      if (!store.getters.isLoggedIn) {
        next({name: 'enter'})
      } else {
        next()
      }
    }
  }, {
    path: '/novo-endereco',
    name: 'newaddress',
    component: NewAddress
  }, {
    path: '/termos',
    name: 'terms',
    component: Terms
  }, {
    path: '/sobre-nos',
    name: 'aboutus',
    component: AboutUs
  }, {
    path: '/privacidade',
    name: 'privacypolicy',
    component: PrivacyPolicy
  }, {
    path: '/como-pedir',
    name: 'howitworks',
    component: HowItWorks
  }, {
    path: '/novo-restaurante',
    name: 'newrestaurant',
    component: NewRestaurant
  }, {
    path: '/restaurantes/:url_name',
    name: 'profile',
    component: Profile,
    props: true
  }, {
    path: '/pedidos/:id',
    name: 'orderdetails',
    component: OrderDetails,
    props: true
  }
  ]
})



